.top {
  min-height: calc(100vh - 80px);
  background-image: url(../../../public/images/top-img.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
}
.top .home_title {
  font-size: 100px;
  text-align: center;
  padding-top: 20px;
}
.top .title {
  padding-top: 7%;
  font-size: 86px;
  margin-bottom: 10px;
  font-weight: 400;
}
.top .subtitel {
  font-size: 36px;
  margin-bottom: 5px;
}
.top .top__text {
  font-size: 13px;
  margin-bottom: 50px;
}
.top .buy__text {
  font-size: 13px;
  padding-top: 16px;
}
.top .buy_btn {
  cursor: pointer;
  border: none;
  background: var(--gradient, linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%));
  display: flex;
  text-transform: uppercase;
  padding: 1px;
}
.top .buy_btn__text {
  color: #F6F4F5;
  padding: 20px 110px;
  text-transform: uppercase;
}
.top .buy_btn__price {
  padding: 20px 105px;
  background-color: #eaeaea;
}

.features {
  padding: 100px 0;
}

.features__titles {
  display: flex;
}

.features__title {
  flex-basis: 300px;
  padding-right: 20px;
}

.features__text {
  max-width: 330px;
  text-transform: uppercase;
  font-size: 36px;
  margin-bottom: 2px;
  background: var(--gradient, linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.tabs {
  display: grid;
  grid-template-columns: 300px auto;
}

.tabs__btn {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.tabs__btn_item {
  font-weight: 700;
  margin-bottom: 20px;
}
.tabs__btn_item--active {
  color: #070707;
}

.tabs__content {
  position: relative;
  min-height: 300px;
}
.tabs__content-item {
  display: grid;
  grid-template-columns: auto 280px;
  grid-template-rows: auto 150px;
  gap: 40px;
  opacity: 0;
  position: absolute;
  transition: opacity 0.3s ease, transform 0.5s ease;
  transform: translateY(100%);
}
.tabs__content-item--active {
  opacity: 1;
  transform: translateY(0);
}

.tabs__content__img {
  grid-row: 1/-1;
  align-self: stretch;
  -o-object-fit: cover;
     object-fit: cover;
}

.tabs__content__box h3 {
  font-weight: 400;
  margin-bottom: 5px;
  font-size: 24px;
}

.videoModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.videoModalContent {
  position: relative;
  width: 100%;
}

.videoModalClose {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #fff;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.video {
  grid-column: 2/3;
  width: 280px;
  height: 150px;
  background-image: url(../../../public/images/content-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 9px 10px;
}
.video .video__btn {
  margin-left: 22px;
}
.video span {
  font-size: 13;
  color: #f6f3f5;
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 66px;
  }
  .subtitel {
    font-size: 24px;
  }
  .home_title {
    font-size: 80px !important;
  }
}
@media screen and (max-width: 640px) {
  .top {
    text-align: center;
  }
  .buy_btn {
    flex-direction: column;
    margin: 0 auto;
  }
  .buy_btn__price {
    width: 100%;
  }
}
@media screen and (max-width: 420px) {
  .top {
    text-align: center;
  }
  .title {
    font-size: 48px;
  }
  .home_title {
    font-size: 60px !important;
  }
  .subtitel {
    font-size: 20px;
  }
  .buy_btn {
    flex-direction: column;
    margin: 0 auto;
  }
}/*# sourceMappingURL=home.module.css.map */