.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    flex-direction: column-reverse;
}

.auth_container {
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.form_container {
    display: flex;
    flex-direction: column;
    transition: transform 0.3s ease-in-out;
    animation: slideIn 0.5s;
}

@keyframes slideIn {
    from {
        transform: translateY(-100%);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.login_page,
.registration_page {
    transform: translateX(0);
    opacity: 1;
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
    position: relative;
}

.login_page.inactive,
.registration_page.inactive {
    transform: translateX(-100%);
    opacity: 0;
    position: absolute;
}

.toggle_button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
    margin: 10px 0;
}

.toggle_button:hover {
    background-color: #0056b3;
}