.categoriesContainer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.categoriesCard {
  width: 30%;
  margin: 20px 0;
  border: 1px solid;
  -o-border-image: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%) 1;
     border-image: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%) 1;
  padding: 10px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.categoriesCardImage {
  max-width: 100%;
  height: 200px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 5px;
}

.categoriesCardTitle {
  font-size: 24px;
  margin-top: 10px;
  font-weight: 400;
}

.categoriesCardButton {
  background-color: #007bff;
  color: #fff;
  padding: 5px 10px;
  border: 1px solid;
  -o-border-image: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%) 1;
     border-image: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%) 1;
  letter-spacing: 0.1rem;
  background: var(--gradient, linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%));
  margin-top: 10px;
  transition: background-color 0.2s;
}

.categoriesCardButton:hover {
  background: var(--gradient, linear-gradient(180deg, #4a304f 0%, rgba(59, 32, 118, 0.93) 100%));
}

@media screen and (max-width: 900px) {
  .categoriesCard {
    width: 45%;
  }
}
@media screen and (max-width: 570px) {
  .categoriesCard {
    width: 100%;
  }
}/*# sourceMappingURL=categories.module.css.map */